@tailwind preflight;
@tailwind components;
@tailwind utilities;

:root {
    --color-bg-primary: #0a2e4e;
    --color-bg-secondary: #192b3c;
    --color-bg-default: #152229;
    --color-bg-inverse: #f7f0ba;

    --color-text-primary: #9cc422;
    --color-text-secondary: #077cc9;
    --color-text-default: #fff;
    --color-text-default-soft: rgba(255, 255, 255, 0.7);
    --color-text-inverse: #373737;
    --color-text-inverse-soft: #6a6a6a;

    --color-link-primary: #9cc422;
    --color-link-inverse: #3c7412;
}

a {
    color: var(--color-link-primary);
    text-decoration: underline;
    cursor: pointer;
}
.text-inverse a {
    color: var(--color-link-inverse);
    text-decoration: underline;
}
a:hover,a:visited {
    opacity: 0.8;
    text-decoration: none;
}


/* misc */
.close-x {
    position: absolute;
    top: 0.25rem;
    right: 0.5rem;
    font-size: 0.75rem;
}
a.close-x {
    text-decoration: none;
    color: var(--color-text-default-soft);
}
